/**
 * @jsx React.DOM
 */

var React = require('react');
var Link = require('react-router').Link;
var termService = require('../services/term-service');
var scheduleService = require('../services/schedule-service');
var pluralize = require('pluralize');

var MyLink = require('./my-link.jsx');

var Nav = React.createClass({displayName: 'Nav',
    getInitialState: function () {
        return {
            selectedTerm: '',
            terms: [],
            studentSchedule: null
        };
    },
    refreshSchedule: function() {
        console.log('refreshing schedule')
        scheduleService.getTermSchedule(this.state.selectedTerm).then(function (schedule) {
            console.log(schedule)
            this.setState({studentSchedule: schedule})
        }.bind(this));

        this.forceUpdate()
    },
    getRegisteredCredits: function() {
        var credits = 0;
        if (this.state.studentSchedule && this.state.studentSchedule.courses) {
            this.state.studentSchedule.courses.forEach(function (course) {
                credits += course.credits;
            });
        }

        return credits;
    },
    componentDidMount: function () {
        termService.getTerms().then(function (terms) {
            this.setState({terms: terms})
        }.bind(this));

        this.refreshSchedule()
    },
    handleChange: function (event) {
        this.setState({selectedTerm: event.target.value});
        this.refreshSchedule()
    },
    render: function () {
        var termOptions = this.state.terms.map(function (term) {
            return (
                React.DOM.option({value: term.termId}, term.termName)
            )
        }.bind(this));

        var registeredCredits = '';
        if (this.state.studentSchedule && (this.getRegisteredCredits() > 0)) {
            console.log('here')
           registeredCredits = React.DOM.li(null, this.getRegisteredCredits(), " ", pluralize('credits', this.getRegisteredCredits()))
        }

        return (
            React.DOM.nav({className: "top-bar"}, 
                React.DOM.ul({className: "title-area"}, 
                    React.DOM.li({className: "name"}, 
                        React.DOM.h1(null, Link({to: "/"}, "Kuali Co"))
                    ), 
                    React.DOM.li({className: "toggle-topbar menu-icon"}, React.DOM.a({href: "#"}, React.DOM.span(null, "Menu")))
                ), 

                React.DOM.section({className: "top-bar-section"}, 
                    React.DOM.ul({className: "left"}, 
                        React.DOM.li({className: "name"}, React.DOM.h1(null, "Course Registration"))
                    ), 

                    React.DOM.ul({className: "left"}, 
                        React.DOM.li({className: "has-dropdown not-click"}, 
                            React.DOM.select({name: "selectedTerm", className: "selectedTerm", onChange: this.handleChange}, 
                              termOptions
                            )
                        )
                    ), 

                    React.DOM.ul({className: "left credits"}, 
                        registeredCredits
                    )
                )
            )
        );
    }

});

module.exports = Nav;



