/**
 * @jsx React.DOM
 */
var React = require('react');
var $ = require('jquery');
require('jquery-ui');


var MyView = React.createClass({displayName: 'MyView',
    getInitialState: function () {
        return {
            courses: []
        };
    },
    componentDidMount: function () {
        this.renderAccordion()
    },
    componentDidUpdate: function () {
        this.renderAccordion()
    },
    renderAccordion: function() {
        $('#cartListing').accordion({
            collapsible: true
        });
    },
    addCourseToCart: function () {
        var newCourse = {
            courseCode: this.refs.addCourseCode.getDOMNode().value,
            section: this.refs.addSection.getDOMNode().value,
            credits: 3
        };
        this.state.courses.push(newCourse);
        this.forceUpdate()

        this.refs.addCourseCode.getDOMNode().value = '';
        this.refs.addSection.getDOMNode().value = '';
    },
    render: function () {
        var cartListing = this.state.courses.map(function (course) {
            return (
                React.DOM.div(null, 
                    React.DOM.h3(null, 
                        React.DOM.div({className: "row"}, 
                            React.DOM.div({className: "medium-offset-3 medium-5 columns"}, course.courseCode, " (", course.section, ")"), 
                            React.DOM.div({className: "medium-4 columns"}, course.credits, " cr")
                        )
                    ), 
                "details"
                )
            )
        }.bind(this));

        return (
            React.DOM.div({className: "cart"}, 
                React.DOM.div({className: "cart-header"}, 
                    React.DOM.div({className: "util-grid-cell"}, 
                        React.DOM.div({className: "cart-header-title"}, "My Cart")
                    ), 
                    React.DOM.div(null, 
                        React.DOM.button({className: "tiny"}, "Register"), 
                    "", 
                        React.DOM.button({className: "tiny secondary"}, "Clear")
                    )
                ), 

                React.DOM.div({className: "cart-add"}, 
                    React.DOM.div({className: "cart-add-contents"}, 
                        React.DOM.div({className: "cart-add-course"}, 
                            React.DOM.label({className: "label-top", htmlFor: "addCourseCode"}, "Course Code"), 
                            React.DOM.input({name: "addCourseCode", size: "9", ref: "addCourseCode"})
                        ), 

                        React.DOM.div({className: "cart-add-section"}, 
                            React.DOM.label({className: "label-top", htmlFor: "addCourseCode"}, "Section"), 
                            React.DOM.input({name: "addSection", size: "4", ref: "addSection"})
                        ), 

                        React.DOM.div({className: "cart-add-button"}, 
                            React.DOM.a({href: "#", className: "tiny button", onClick: this.addCourseToCart}, "Add to Cart")
                        )
                    )
                ), 

                React.DOM.div({id: "cartListing", className: "cart-listing"}, 
                    cartListing
                )
            )
        );
    }
});

module.exports = MyView;


